import Ccanvas from '../service/canvas';

export default class ImagePos {
    constructor(canvas, context, x, y, color) {
        this.canvas = canvas;
        this.context = context;
        this.x = x;
        this.baseX = x;

        this.y = y;
        this.baseY = y;

        this.color = color;
        this.density = (Math.random() * 10) + 2;
        this.size = 4;
        this.mouseRadius = 80;
    }
    draw() {
        this.context.fillColor = this.color;
        this.context.beginPath().createArc(this.x, this.y, this.size, 0, 360).closePath().fill();
    }
    update(offsetX, offsetY) {
        this.context.fillColor = this.color;
        let dx = this.x - offsetX;
        let dy = this.y - offsetY;
        let r = Math.sqrt(dx * dx + dy * dy);
        let fx = dx / r;
        let fy = dy / r;
        const maxDistane = 80;
        let force = (maxDistane - r) / maxDistane;
        if (force < 0) force = 0;
        let directionX = fx * force * this.density * 0.6;
        let directionY = fy * force * this.density * 0.6;
        if (r < this.size + this.mouseRadius) {
            this.x += directionX;
            this.y += directionY;
        } else {
            if (this.x != this.baseX) {
                let dx = this.x - this.baseX
                this.x -= dx / 10;
            }
            if (this.y != this.baseY) {
                let dy = this.y - this.baseY
                this.y -= dy / 10;
            }
        }


        // if (r < (this.size + this.mouseRadius)) {
        //     this.x += fx;
        //     this.y += fy;
        // } else {
        //     let dx1 = this.x - this.baseX;
        //     let dy1 = this.y - this.baseY;
        //     let r0 = Math.sqrt(dx1 * dx1 + dy1 * dy1);
        //     if (r0 != 0) {
        //         this.x -= (dx1*1.5);
        //         this.y -= (dy1 *1.5);
        //     }
        // }

        this.draw()

    }
}


function imageRender() {
    for (let y = 0, y2 = imageData.height; y < y2; y += 8) {
        for (let x = 0, x2 = imageData.width; x < x2; x += 8) {
            if (imageData.data[(y * 4 * imageData.width + x * 4) + 3] > 0) {
                let positionx = x;
                let positiony = y;
                let color = `rgb(${
            imageData.data[y * 4 * imageData.width + x * 4]
          },
                              ${
                                imageData.data[
                                  (y * 4 * imageData.width + x * 4
                                  )+1] 
                              },
                              ${
                                imageData.data[
                                 ( y * 4 * imageData.width + x * 4
                                 )+2]
                              }
             )`;
                ImageArrayObj.push(
                    new ImagePos(canvas, context, positionx, positiony, color, image)
                );
            }
        }
    }
    animation();
}

function animation() {
    // this.context.clearRect(0, 0, this.board.width, this.board.height);
    context.fillColor = 'rgba(255,255,255,0.5)';
    context.fillRect(0, 0, canvas.width, canvas.height)
    let length = ImageArrayObj.length;
    let index = 0;
    while (index < length) {
        ImageArrayObj[index].update(xPos, yPos);
        index++;
    }
    requestAnimationFrame(animation);
}
var canvas = null;
var context = null;
var imageData = null;
var image = [];
var ImageArrayObj = [];
var xPos = 1000;
var yPos = 1000;
var cCanvas = new Ccanvas();
self.onmessage = function (evt) {
    if (evt.data.isInitial) {
        canvas = evt.data.canvas;
        context = cCanvas.createCanvas(canvas, '2d');
        imageData = evt.data.image;
        imageRender()
    } else {
        // console.log(evt.data)
        if (evt.data.type != 'mouseleave' && evt.data.type != 'touchend') {
            xPos = evt.data.mouse.x;
            yPos = evt.data.mouse.y
        } else {
            xPos = 1024;
            yPos = 1024;
        }
    }
}